.checkoutProduct {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}
.checkoutProduct_info {
    margin-left: 20px;

}
.checkoutProduct_image {
    object-fit: contain;
    width: 200px;
    height: 200px;
}
.checkoutProduct_rating {
    display: flex;
}
.checkoutProduct_title {
    font-size: 17px;
    font-weight: 700;
}
.checkoutProduct_info > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}